<template>
  <div class="jumbotron px-5 py-4">
    <h2 class="text-center mt-3">Register a new user</h2>
    <p class="text-secondary text-center mb-5">Just choose a username (no password needed) and use it to log your everyday exercise.</p>
    <form action="api/exercise/new-user" @submit.prevent="addUser">
      <div class="form-group row">
        <div class="input-group">
          <div class="input-group-prepend col-sm-4 col-md-2 px-0">
            <span class="input-group-text w-100">Username</span>
          </div>
          <input
            id          = "username"
            class       = "form-control col-sm-8 col-md-10"
            type        = "text"
            v-model     = "username"
            @input      = "resetAlerts"
            minlength   = "3"
            maxlength   = "30"
            placeholder = "Enter a username for you"
            required
            autofocus
          />
          <button type="submit" class="btn btn-block btn-primary mt-5">Sign Up!</button>
        </div>
      </div>
    </form>
    <Alert :output="output" :error="error" />
  </div>
</template>

<script>
import Alert from '../components/Alert';
import axios from 'axios';

export default {
  name: 'SignUp',
  components: { Alert },
  data( ) {
    return {
      username: '',
      output  : '',
      error   : ''
    }
  },
  methods: {
    resetAlerts ( ) {
      this.output = this.error = '';
    },
    addUser ( e ) {
      e.preventDefault( );
      this.resetAlerts( );
      const query = {
        username: this.username
      }
      axios
        .post( `/api/exercise/new-user`, query )
        .then( data => {
          if ( data.data.username )
            return this.output = `The username <strong>${this.username}</strong> has been registered. Go ahead, begin <a href="/">adding exercises</a> to your log!`
          this.output = data.data
        } )
        .catch( error => {
          if ( error.response )
            return this.error = error.response.data.error;
          this.error = 'Oops! Something went wrong.'
        } );
    }
  }
}
</script>